body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'adriane', Georgia, serif;
}

.App-body {
  background-color: white;
  border: 10px solid #004D71;
  margin: 0px;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #004D71;
}

.App-link {
  color: #61dafb;
}

.menu-container {
  float: right;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: white;
  padding-top: 2em;
  padding-bottom: 2em;
  font-size: 70%;
}

li {
  float: left;
}

li a {
  display: block;
  color: #494c4e;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #494c4e;
  color: white;
}
.footer-container {
    display: -webkit-flex;
    display: flex;
    position: fixed;
    left: 0;
    bottom: 0;
    height: 30px;
    width: 100%;
    background-color: red;
    color: white;
    text-align: center;
}

.footer-item {
    background-color: green;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
  }

.footer-item img {
    width: 25%;
    float: center;
    -webkit-align-items: center;
            align-items: center;
   
}
img {
    display: block;
    width: 70%;
    height: auto;
    margin-top: 2em;
    margin-bottom: 2em;
    background-color: #f0eeee;
    margin-left: auto;
    margin-right: auto;
}
